.mainPageBackgroundContainer{
    display:flex; 
}
.mainPleftSectionContainer{
    display:flex;
    flex-direction:column;
    align-items:center;
    padding:30px;
    border-right:2px solid #B2BEB5;
    width:25%;
    height: calc(100vh - 80px);
    
}
@media screen and (max-width:768px){
