.logoContainer{
    display:flex;
    margin-bottom:0px;
}
.logoProp{
    height:50%;
}
.logoTextprop{
    margin-top:10px;
    height:100px;
}
.loginPName{
    margin-right:40px;
}
@media screen and (max-width:768px){
