    }
}

.CommandBoxHeading{
    text-align:center;
    font-family:"Roboto";
    font-size:20px;
    font-weight:500;
}
.CommandInput{
    width:250px;
    margin-left:20px;
}
.search-container {
    display: flex;
    align-items: center;
  }
  
.search-input {
    padding: 8px;
    outline: none;
    border: 1px solid #909090;
    line-height:15px;
    width:95%;
}

  
.search-input::placeholder {
    color: #909090;
  }
  
.search-button {
    border: none;
    cursor: pointer;
    border: 1px solid #909090;
    width: 50px;
    line-height: 15px;
    padding:4px;
  }
  
.search-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
}

li{
    margin-bottom:8px;
}
details{
    margin-bottom:8px;
    margin-left:10px;
    margin-top:10px;
}



.mainPrightSectionContainer{
    width:75%;
}
@media screen and (max-width:768px){
    .mainPrightSectionContainer{
        width:100%;
    }
}

.mainPrightSectionTopBar{
    border-bottom:2px solid #B2BEB5;
    height:100px;
    display:flex;
    align-items:center;
    justify-content:flex-end;
}
@media screen and (max-width:768px){
    .mainPrightSectionTopBar{
        justify-content:space-between;
        padding-left:20px;
        padding-right:20px;
    }
}
.signoutButton{
    background-color:blue;
    outline:none;
    border:none;
    color:#fff;
    padding:12px;
    border-radius:10px;
}
.rightSectionBottomContainer{
    padding:30px;

}
.mainPtopInputContainer{
    width:95%;
    height:90px;
    outline:black;
    border:1px solid black;
    padding:15px;
    font-size:20px;
    margin-top:8px;
}
.mainPbuttonsContainer{
    display:flex;
    justify-content:flex-end;
    gap:10px;
    margin-top:10px;
    padding-right:10px;
    margin-bottom:0px;
}
@media screen and (max-width:500px){
    .mainPbuttonsContainer{
        gap:5px;
        padding-right:0px;
    }
}
.button{
    text-align:center;
    width:70px;
    height:40px;
    outline:none;
    border:none;
    color:#fff;
    border-radius:8px;
    font-size:15px;
}
.startButton{
    background-color:green;
}
.generate{
    background-color: black;
    color: #fff;
    width:200px !important;
}
.generate:hover{
    background-color:aqua;
    color: black;
}
.generateLink{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}

/* Add styles for the popup */
.generateLinkPop {
  position: fixed;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  background-color: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 9999;
  height: 300px;
  width: 600px;
}
.userLink{
    border: 1px solid red;
    height: 30px;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    padding: 5px;
    position: relative;
}
.userLink span{
    position: absolute;
    right: 10px;
}
.userLink span:hover{
    cursor: pointer;
}
.generateLinkPop p {
  text-align: center;
}

.generateLinkPop button {
  display: block;
  position: fixed;
  bottom: 10px;
  left:45%;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
}

.generateLinkPop button:hover {
  background-color: #555;
}

/* Add styles for the overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
  z-index: 9998; /* Ensure it's below the popup */
}

.stopButton{
    background-color:red;
}
.resetButton{
