.mainBackgroundContainer{
    display:flex;

}
.leftSectionContainer{
    display:flex;
    flex-direction:column;
    align-items:center;
    padding:30px;
    border-right:2px solid #B2BEB5;
    width:25%;
    height:100vh;
}
/* @media screen and (max-width:768px){
    .leftSectionContainer{
        display:none;
    }
} */
.heading{
    font-family:"Roboto";
    font-size:30px;
}
.headingText{
    font-family:"Roboto";
    font-size:20px;
    font-weight:500;
}


.rightSectionContainer{
    width:75%;
}
/* @media screen and (max-width:768px) {
    .rightSectionContainer{
        width:100%;
    }
    
} */

.rightSectionTopBar{
    border-bottom:2px solid #B2BEB5;
    height:100px;
    display:flex;
    align-items:center;
    justify-content:flex-end;
}
.signoutButton{
    background-color:blue;
    outline:none;
    border:none;
    color:#fff;
    padding:12px;
    border-radius:10px;
}
.rightSectionBottomContainer{
    padding:30px;

}
.topInputContainer{
    width:95%;
    height:90px;
    outline:black;
    border:1px solid black;
    padding:15px;
    font-size:20px;
}
.buttonsContainer{
    display:flex;
    justify-content:flex-end;
    gap:10px;
    margin-top:20px;
    margin-bottom:30px;
    padding-right:10px;
}
@media screen and (max-width:500px){
    .buttonsContainer{
        gap:5px;
        padding-right:0px;
    }
}
.button{
    text-align:center;
    width:70px;
    height:40px;
    outline:none;
    border:none;
    color:#fff;
    border-radius:8px;
    font-size:15px;
}
.startButton{
    background-color:green;
}
.stopButton{
    background-color:red;
}
.resetButton{
    background-color:blue;
}
.bottomInputContainer{
    width:95%;
    height:150px;
    outline:black;
    border:1px solid black;
    padding:15px;
    font-size:20px;
    margin-top:100px;
}

